:root {
  --font-jost: "Jost", sans-serif;
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --gold: hsl(26, 35%, 60%);
  --gray: hsl(240, 1%, 23%);
  --lightgray: hsla(240, 1%, 23%, 0.692);
  --black: hsl(0, 0%, 8%);
  --blueish: hsl(240, 17%, 17%);
  --darkblue: hsl(240, 35%, 9%);
  --blackish: hsl(240, 35%, 9%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-jost);
}

figure {
  margin: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--black);
  text-decoration: none;
}

.card {
  font-size: 1.25rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 5vw);
  row-gap: 0px;
  column-gap: 0px;
}
.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center;
}
.gallery__item--1 {
  grid-column-end: 5;
  grid-column-start: 1;
  grid-row-end: 6;
  grid-row-start: 1;
}
.gallery__item--2 {
  grid-column-end: 5;
  grid-column-start: 3;
  grid-row-end: 9;
  grid-row-start: 6;
}
.gallery__item--3 {
  grid-column-end: 9;
  grid-column-start: 5;
  grid-row-end: 9;
  grid-row-start: 1;
}
.gallery__item--4 {
  grid-column-end: 3;
  grid-column-start: 1;
  grid-row-end: 9;
  grid-row-start: 6;
}

.topbar {
  width: 100%;
  background-color: var(--black);
  height: 2.5rem;
  font-size: 0.875rem;
}
.topbar__wrapper {
  height: 100%;
}
.topbar__info {
  color: white;
  letter-spacing: 0.1rem;
  margin-right: 3.75rem;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.topbar__info:hover {
  color: var(--gold);
}
.topbar__icons {
  margin-right: 3.75rem;
  letter-spacing: 0.1rem;
}
.topbar__icons .phone {
  margin-right: 3.75rem;
}
.topbar__icons .phone .fa-mobile-alt {
  margin-right: 0.5rem;
}
.topbar a {
  color: white;
  transition: all 0.1s ease-in;
}
.topbar a:hover {
  color: var(--gold);
}

.header {
  height: 5rem;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.header__logo img {
  height: 5rem;
}
.header__links {
  flex: 0.3;
  justify-content: space-evenly;
  list-style: none;
  letter-spacing: 0.125rem;
  font-weight: 400;
}
.header a {
  color: var(--blackish);
  transition: all 0.1s ease-out;
}
.header a:hover {
  color: var(--gold);
}

.hero {
  overflow: hidden;
}
.hero__image {
  height: 85vh;
  width: 100vw;
  position: relative;
  background-size: cover;
  background-position: center;
  background-image: url("../../../images/Coffe-shop.jpg");
}
.hero__overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.65);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hero__overlay h1 {
  font-size: 3rem;
  font-family: var(--font-poppins);
  margin-bottom: 0;
  letter-spacing: 0.075rem;
  font-weight: 500;
  text-transform: capitalize;
  width: 80%;
  text-align: center;
}
.hero__overlay p {
  font-size: 1rem;
}

.entry-text {
  flex-direction: column;
}
.entry-text__wrapper {
  text-align: center;
  width: 32.25rem;
  margin: 4.6875rem 0;
}
.entry-text__tagline {
  text-transform: uppercase;
  letter-spacing: 0.075rem;
  color: var(--gold);
  font-weight: 600;
}
.entry-text__divider {
  background-color: var(--gold);
  width: 3px;
  height: 3.125rem;
  margin: 0 auto;
}
.entry-text__info {
  font-weight: 400;
  color: var(--gray);
  margin-top: 2.5rem;
}
.entry-text h2 {
  margin-bottom: 1.25rem;
  font-size: 2.375rem;
}

.coffe-menu {
  width: 95%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: auto;
  grid-auto-flow: column;
  background-color: #f7f2ee;
}
.coffe-menu__left {
  margin: 0px 0px 64px 64px;
}
.coffe-menu__left__header {
  text-align: left;
  text-transform: uppercase;
  color: var(--gray);
  font-size: 1.5rem;
  margin-top: 4rem;
}
.coffe-menu__left__header hr {
  margin-bottom: 30px;
  margin-top: -20px;
  height: 0.3125rem;
  background-color: var(--gray);
}
.coffe-menu__left__body__inner-box {
  margin: 5px 0px;
}
.coffe-menu__left__body__inner-box__wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.coffe-menu__left__body__inner-box__wrapper__size {
  margin-left: 50%;
  width: 50%;
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
}
.coffe-menu__left__body__inner-box__wrapper__title {
  width: 50%;
  margin: 0.3125rem;
  text-transform: uppercase;
  color: var(--gray);
  font-weight: 600;
  letter-spacing: 0.0625rem;
}
.coffe-menu__left__body__inner-box__wrapper__price {
  font-weight: 600;
  color: var(--lightgray);
  width: 50%;
  display: flex;
  justify-content: space-between;
  margin: 0.3125rem;
}
.coffe-menu__right {
  margin: 0px 0px 64px 64px;
}
.coffe-menu__right__header {
  background-color: var(--gray);
  text-align: left;
  text-transform: uppercase;
  color: white;
  font-size: 1.5rem;
  margin-top: 4rem;
  padding: 1.25rem;
}